.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  padding: 20px;
  background-color: white;
  z-index: 1;
  border-radius: 3px;
  box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
  transition: 200ms ease-in;
  cursor: pointer;
}

.product:hover {
  transform: scale(1.04);
}

.product__rating {
  display: flex;
}

.product > img {
  max-height: 200px;
  width: 100%;
  object-fit: contain;
  margin-bottom: 15px;
}

.product > button {
  background: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
  padding: 4px 20px;
  border-radius: 3px;
  font-weight: 500;
  cursor: pointer;
}

.product > button:active {
  outline: none;
  border: none;
}

.product > button:focus {
  outline: 0;
}

.product > button:hover {
  background-color: #a88734;
}

.product__price {
  margin-top: 5px;
}

.product__info {
  height: 100px;
  margin-bottom: 15px;
}
